import * as React from 'react';
import { graphql, navigate } from 'gatsby';
import Box from '@mui/material/Box';
import Chip from '@mui/material/Chip';
import Typography from '@mui/material/Typography';
import LocalOfferIcon from '@mui/icons-material/LocalOffer';
import StyleIcon from '@mui/icons-material/Style';
import Layout from '../components/layout';

const TagsPage = ({ data, location }) => {
  const { group } = data.allMarkdownRemark;

  return (
    <Layout location={location} title='タグ一覧'>
      <Typography
        variant='h1'
        sx={{
          display: 'flex',
          alignItems: 'center',
          mb: 3
        }}
      >
        <StyleIcon /> タグ一覧
      </Typography>
      <Box>
        {group
          .sort((first, second) => second.totalCount - first.totalCount)
          .map(({ fieldValue, totalCount }) => (
            <Chip
              key={fieldValue}
              label={`${fieldValue} (${totalCount})`}
              icon={<LocalOfferIcon />}
              size={totalCount > 1 ? 'medium' : 'small'}
              onClick={() => navigate(`/tags/${fieldValue}/`)}
              sx={{
                mr: 2,
                mb: 2
              }}
            />
          ))}
      </Box>
    </Layout>
  );
};

export default TagsPage;

export const pageQuery = graphql`
  query {
    allMarkdownRemark(limit: 2000) {
      group(field: frontmatter___tags) {
        fieldValue
        totalCount
      }
    }
  }
`;
